import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
const router=new Router({
	mode:'hash',//history or hash
	routes: [
	  {
	   path: '/goodsinfo',
	    name: 'goodsinfo',
	    component:()=>import('@/components/GoodsInfo.vue')
	  },
	  {
		  path:'/',
		  name:'home',
		  component:()=>import('@/components/Home.vue')
	  }
	]
});

export default router;