import Vue from 'vue'
import vuex from 'vuex'
Vue.use(vuex)
const store= new vuex.Store({
  state:{
    count: 0,
    me: {
      name: 'huangenai',
      age: 22,
      sex: '女'
    },
      list: [{name: 'hea', age: 22}, {name: 'cpq', age: 23}]
  },
  getters:{
    getlist:function(state){/* 通过this.$store.getters.getlist 调用 */
      return state.list.filter(item => item.age >= 23)
    },
    getcount:function(state){
      return state.count;
    }
  },
  mutations:{/* 通过this.$store.commit 调用 */
      increment:function(state,count) {
        state.count=count;
      }
   },
   actions:{/* 通过this.$store.dispatch('setCount', {count: 123}) 主要用于提交mutations */
     setcount:function({commit},data){
       commit('increment',data.count);
     }
   }
})
export default store
